.backdrop {
	background: rgba(0, 0, 0, 0.7);
	position: fixed;
	width: 100vw;
	height: 100vh;
	z-index: 300;
}
.card {
	padding: 25px;
	z-index: 200;
	width: 70%;
	height: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	background-color: white;
	transform: translate(-50%, -50%);
}
